* {
   font-family: 'Quicksand', sans-serif;
}

.nav-pills {
   margin-bottom: 25px;
}

.nav-pills .nav-link {
   color: #111;
   outline: none;
   border: 1px solid rgba(64, 64, 64, 0.17);
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
   margin-right: 6px;
   -webkit-box-shadow: 0 2px 3px rgba(0,0,0,0.16), 0 1px 4px rgba(0,0,0,0.23);
           box-shadow: 0 2px 3px rgba(0,0,0,0.16), 0 1px 4px rgba(0,0,0,0.23)
}

.nav-pills .nav-link:hover {
   -webkit-box-shadow: none;
           box-shadow: none;
}

.nav-pills .nav-link .active {
   border: none;
}

.tab-content {
   margin-top: 25px;
}

.jumbotron:hover {
   -webkit-box-shadow: none;
           box-shadow: none;
}

.jumbotron {
   background-image: url('images/rocket.jpg');
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-position: center;
   padding: 5rem 2rem;
   color: #fff;
   -webkit-transition: -webkit-box-shadow .5s ease;
   transition: -webkit-box-shadow .5s ease;
   -o-transition: box-shadow .5s ease;
   transition: box-shadow .5s ease;
   transition: box-shadow .5s ease, -webkit-box-shadow .5s ease;
   -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
           box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.jumbotron-content {
   padding-top: 10px;
}

.avatar {
   -webkit-box-shadow: 0px 0px 5px #fff;
           box-shadow: 0px 0px 5px #fff;
}

.short-bio {
   text-align: left;
   margin: 0;
   padding-top: 15px;
   font-size: 19px;
   font-weight: 300;
}

.nav-content {
   padding-top: 10px;
   padding-bottom: 10px;
   font-size: 3em;
   text-align: center;
   font-family: 'Arizonia', cursive;
}

/* About */
.about-bio {
   text-align: justify;
}


